.topic {
	padding-top: 80px;
	background-color: $dark;
	@include md {
		padding-top: 60px; }
	@include sm {
		padding-top: 40px; }

	&__inner {
		display: flex;
		flex-direction: column;
		align-items: center; }

	&__title {
		letter-spacing: 1px; }

	&__discr {
		font-size: 16px;
		color: #f8fdff;
		margin-top: 16px;
		letter-spacing: 0.5px;
		@include md {
			font-size: 14px; } }

	&__links-list {
		display: flex;
		margin-top: 16px; }

	&__links-item {
		&:not(:last-child) {
			&:after {
				content: "|";
				font-size: 24px;
				color: $sky-blue;
				margin-right: 6px; } }
		@include md {
			&:not(:last-child) {
				&:after {
					font-size: 20px; } } }
		@include sm {
			&:not(:last-child) {
				&:after {
					font-size: 16px; } } } }

	&__links-link {
		font-size: 24px;
		color: $sky-blue;
		&:hover {
			opacity: 0.7; }
		&:active {
			opacity: 0.85; }
		@include md {
			font-size: 20px; }
		@include sm {
			font-size: 16px; } }

	&__obj-list {
		position: relative;
		width: 100%;
		max-width: 1068px;
		margin: 0 auto;
		height: 373px;
		@include md {
			max-width: 767px; }
		@include sm {
			display: flex;
			flex-wrap: wrap;
			height: 273px; } }

	&__obj-item {
		position: absolute;
		@include sm {
			position: static;
			width: 25%;
			display: flex;
			justify-content: center;
			align-items: center; } }

	&__obj-wrapper {
		width: 96px;
		height: 96px;
		background-color: #2e3b44;
		border-radius: 8px;
		box-shadow: 0 5px 36px 0 rgba($black, 0.75);
		&:hover {
			opacity: 0.7; }
		&:active {
			opacity: 0.85; }
		@include md {
			width: 64px;
			height: 64px; } }


	&__obj_android-tv {
		top: 21px;
		left: 152px;
		.topic__obj-title {
			top: 28px;
			left: -70px; }
		@include md {
			top: 70px;
			.topic__obj-title {
				top: 14px;
				left: -55px; } } }

	&__obj_apple-tv {
		top: 69px;
		left: 360px;
		.topic__obj-title {
			bottom: -16px;
			left: 24px; }
		@include md {
			top: 90px;
			left: 320px;
			.topic__obj-title {
				width: 42px;
				bottom: -10px;
				left: 13px; } } }

	&__obj_web {
		top: 43px;
		right: 254px;
		.topic__obj-title {
			top: 24px;
			right: -30px; }
		@include md {
			top: 78px;
			right: 210px;
			.topic__obj-title {
				top: 14px;
				right: -24px; } } }

	&__obj_beacons {
		top: 43px;
		right: 50px;
		.topic__obj-title {
			text-align: center;
			bottom: -31px;
			left: 31px; }
		@include md {
			top: 78px;
			.topic__obj-title {
				bottom: -22px;
				left: 16px; } } }

	&__obj_iot {
		bottom: 118px;
		right: 408px;
		.topic__obj-title {
			top: 19px;
			left: -29px; }
		@include md {
			right: 258px;
			.topic__obj-title {
				top: 14px;
				left: -20px; } } }


	&__obj_phones-tablets {
		bottom: 102px;
		left: 14px;
		.topic__obj-title {
			top: 21px;
			right: -76px;
			width: 75px;
			text-align: center; }
		@include md {
			left: 84px;
			.topic__obj-title {
				top: 14px;
				right: -65px; } } }

	&__obj_iphone-ipad {
		bottom: 32px;
		left: 246px;
		.topic__obj-title {
			top: -19px;
			right: 10px; }
		@include md {
			left: 286px;
			.topic__obj-title {
				top: -24px; } } }

	&__obj_wearables {
		bottom: 41px;
		right: 186px;
		.topic__obj-title {
			bottom: 32px;
			left: -69px; }
		@include md {
			right: 86px;
			.topic__obj-title {
				bottom: 14px;
				left: -55px; } } }


	//&__obj_

	&__obj-link {
		position: absolute; }

	&__obj-icon {}

	&__obj-image {
		width: 96px;
		height: 96px;
		@include md {
			width: 64px;
			height: 64px; } }

	&__obj-title {
		position: absolute;
		font-size: 10px;
		color: $sky-blue;
		text-transform: uppercase;
		letter-spacing: 0.5px;
		@include md {
			font-size: 8px; }
		@include sm {
			position: static;
			display: block;
			text-align: center;
			max-width: 64px; } }

	&__descr {
		margin-top: 2px;
		font-size: 16px;
		color: $white; }

	&__btn {
		margin-bottom: 40px;
		margin-top: 17px;
		text-transform: uppercase;
		font-weight: 600; } }












