@mixin roboto {
  font-family: 'Roboto', Arial, -apple-system , BlinkMacSystemFont, 'Oxygen', sans-serif; }

@mixin notosans {
  font-family: 'Noto Sans', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; }

@mixin permanent {
  font-family: 'Permanent Marker', cursive; }
@mixin playfairdisplay {
  font-family: 'Playfair Display', serif; }

@mixin shadow($val) {
  box-shadow: 0 1.2rem 4.2rem 1.2rem rgba(0, 0, 0, $val); }
