html {
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
  height: 100%;
  @include selection {
    background: $light-navy;
    color: #fff; } }

body {
  background: #fff;
  line-height: 1.4;
  font-size: 2.1rem;
  @include roboto;
  height: 100%;
  font-weight: 400;
  min-width: 320px;
  &.is-hidden {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    overflow: hidden; } }

a {
  text-decoration: none; }

.out {
  overflow: hidden;
  height: 100%; }

[name="google_conversion_frame"] {
  display: none; }

.tablet-hide {
  @include r($lg) {
    display: none; } }
.mobile-hide {
  @include r($sm) {
    display: none; } }

.l-center {
  position: relative;
  margin: 0 auto;
  padding: 0 8rem;
  max-width: 176rem;
  @include r($lg) {
    padding: 0 4rem; }
  @include r($lg) {
    padding: 0 2rem; }
  &_small {
    padding: 0 7rem;
    max-width: 115.8rem;
    @include r($md) {
      padding: 0 6rem; }
    @include r($sm) {
      padding: 0 1rem; } }
  &_sm {
    padding: 0 9rem;
    max-width: 114rem;
    @include r($md) {
      padding: 0 6rem; }
    @include r($sm) {
      padding: 0 1rem; } }
  &_petty {
    max-width: 84rem;
    padding: 0 1.5rem; }
  &_normal {
    padding: 0 7rem;
    max-width: 118rem;
    @include r($md) {
      padding: 0 6rem; }
    @include r($sm) {
      padding: 0 1rem; } }
  &_tiny {
    padding: 0 7rem;
    max-width: 108rem;
    @include r($md) {
      padding: 0 6rem; }
    @include r($sm) {
      padding: 0 1rem; } }
  &_fluid {
    padding: 0 1rem; }
  &_inner {
    padding: 0 8rem;
    max-width: 146rem;
    @include r($xlg) {
      padding: 0 4rem; } }
  &_def {
    padding: 0 7rem;
    max-width: 114rem;
    @include r($md) {
      padding: 0 6rem; }
    @include r($sm) {
      padding: 0 1rem; } }
  &_services {
    padding: 0 1.5rem;
    max-width: 105.4rem;
    @include r($lg) {
      padding: 0 5rem; }
    @include r($sm) {
      padding: 0 2rem; } } }

