.row {
  margin: 0 -15px;
  @include clr; }
.col {
  float: left;
  padding: 0 15px;
  &-25 {
    width: 25%; }
  &-33 {
    width: 33.3%; }
  &-50 {
    width: 50%; }
  &-lg-33 {
    @include r($lg) {
      width: 33.3%; } }
  &-md-50 {
    @include r($md) {
      width: 50%; } }
  &-sm-100 {
    @include r($sm) {
      width: 100%; } } }

.text-center {
  text-align: center; }

.text-left {
  text-align: left; }

.text-right {
  text-align: left; }

.bg-anakiwa {
  background-color: $anakiwa; }
.bg-light-teal {
  background-color: $light-teal; }
.bg-pale-sky-blue {
  background-color: $pale-sky-blue; }
.logo {
  @include hide-text;
  img {
    display: block; } }

.field-row {
  display: flex;
  justify-content: space-between;
  @include r($sm) {
    flex-direction: column; }
  .field {
    width: 47.6%;
    @include r($sm) {
      width: 100%; } } }

.field {
  position: relative;
  display: block;
  margin-bottom: 4.8rem;
  &__input {
    display: block;
    padding: 1rem 1.5rem;
    width: 100%;
    font-size: 2.4rem;
    background: none;
    @include roboto;
    border-bottom: 2px solid #b2bec4;
    .section_blue & {
      color: $white;
      border-color: $white; }
    @include r($lg) {
      padding: .8rem 1.5rem; }
    &:focus {
      ~ .field__bar {
        &:before,
        &:after {
          width: 50%; } } }
    &:focus,
    &:valid,
    &.is-filled {
      ~ .field__name {
        top: -22px;
        font-size: 1.8rem;
        color: $ocean-blue;
        .section_blue & {
          color: lighten($ocean-blue, 40); } } } }
  &__name {
    position: absolute;
    left: 0;
    right: 0;
    text-align: left;
    top: 10px;
    pointer-events: none;
    transition: 0.2s ease all;
    color: #b2bec4;
    font-size: 2.1rem;
    .section_blue & {
      color: $white; }
    .form_small {
      font-size: 1.7rem; } }

  &__bar {
    position: relative;
    display: block;
    &:before,
    &:after {
      content: '';
      height: 2px;
      width: 0;
      bottom: 0;
      position: absolute;
      background: $ocean-blue;
      transition: 0.2s ease all;
      .section_blue & {
        background-color: lighten($ocean-blue, 40); } }
    &:before {
      left: 50%; }
    &:after {
      right: 50%; } }
  &_dark {
    .field__input {
      border-bottom-color: $ocean-blue;
      &:focus,
      &:valid {
        ~ .field__name {
          color: $sky-blue; } } }
    .field__name {
      color: $ocean-blue; }
    .field__bar {
      &:before,
      &:after {
        background: $sky-blue; } } } }

.btn-form {
  display: inline-block;
  padding: 0 1rem;
  min-width: 25.9rem;
  height: 4.4rem;
  box-shadow: 0 0 0.9rem 0.1rem rgba(7, 33, 50, 0.51);
  border-radius: 10rem;
  background-color: $ocean-blue;
  color: #fff;
  width: 18.5rem;
  font-size: 2.1rem;
  @include roboto;
  font-weight: 900;
  letter-spacing: 2px;
  text-transform: uppercase;
  transition: background .3s;
  &:hover {
    background: $light-navy; }
  &:active {
    box-shadow: none; } }

.soc {
  display: flex;
  align-items: center;
  justify-content: center;
  a {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #fff;
    border-right-width: 0;
    width: 5rem;
    height: 5rem;
    transition: background .3s;
    overflow: hidden;
    @include r(760) {
      width: 4.3rem;
      height: 4.3rem; }
    &:last-child {
      border-right-width: 1px; }
    &:hover {
      background: #fff;
      .icon {
        fill: $sky-blue; } } }
  .icon {
    fill: #fff;
    transition: fill .3s;
    &-fb {
      font-size: 2.1rem; }
    &-tw {
      font-size: 1.8rem; }
    &-in {
      font-size: 2rem; } }
  &_circles {
    justify-content: flex-start;
    a {
      width: 5.4rem;
      height: 5.4rem;
      border-radius: 50%;
      border-right-width: 1px;
      &:not(:last-child) {
        margin-right: 2.2rem;
        @include r($sm) {
          margin-right: 1rem; } } }
    .icon {
      &-fb {
        font-size: 3rem; }
      &-tw {
        font-size: 2.3rem; }
      &-in {
        font-size: 2.7rem; }
      &-github {
        margin-top: auto;
        margin-bottom: -.2rem;
        font-size: 3.7rem; }
      &-medium {
        font-size: 2.8rem; } } } }

.contacts {
  display: flex;
  justify-content: center;
  align-items: center;
  @include r(1024) {
    flex-direction: column; }
  li {
    width: 34.5rem;
    @include r(1370) {
      width: 30rem; }
    @include r(1024) {
      margin: .5rem 0;
      width: auto; }
    div {
      @include r(1024) {
        display: inline-block;
        padding: 0 2.5rem;
        vertical-align: middle; } } }
  a {
    color: #fff;
    font-weight: 700;
    transition: color .3s;
    &:hover {
      color: $anakiwa; } }
  &__item {
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: .8rem; } }
  &__item:hover &__icon {
    .icon {
      fill: $anakiwa; } }
  &__icon {
    margin-right: 2.4rem;
    font-size: 0;
    text-align: center;
    flex: 0 0 20px;
    .icon {
      font-size: 2rem;
      transition: fill .2s;
      &-mail {
        font-size: 1.6rem; } } }
  &__title {
    font-size: 2.4rem;
    font-weight: 400;
    @include r($sm) {
      font-size: 1.8rem; } }
  &_list {
    display: block; } }

.subscribe {
  position: relative;
  iframe {
    width: 100% !important; }
  &__input,
  .hs-input {
    width: 100%;
    max-width: 100%;
    height: 5rem;
    padding: 0 50px 0 10px;
    border: 1px solid #fff;
    background: transparent;
    font-size: 1.8rem;
    color: #FFF;
    &::placeholder {
      color: #fff; }
    &:focus {
      &::placeholder {
        font-size: 0; } }
    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus {}
    &:-webkit-autofill {
      -webkit-text-fill-color: #fff !important;
      background-color: transparent !important;
      transition: background-color 5000s ease-in-out 0s; } }
  .hs-richtext,
  .hs-form-booleancheckbox label {
    color: #fff;
    font-size: 12px;
    cursor: pointer;
    user-select: none; }
  .hs-richtext {
    line-height: 1.2;
    a {
      color: #fff;
      border-bottom: 1px solid;
      transition: border-color .3s;
      &:hover {
        border-color: transparent; } } }
  .hs-form-booleancheckbox .hs-form-required {
    color: #FFF; }
  &__btn {
    display: none; }
  .hbspt-form + .subscribe__btn {
    display: block; }
  &__btn,
  .hs-button {
    position: absolute;
    top: 0;
    right: 0;
    width: 5rem;
    height: 5rem;
    background: transparent !important;
    border: none;
    font-size: 0;
    &:hover {
      background-color: transparent; }
    .icon {
      font-size: 1rem;
      fill: #fff; } }
  .hs-button {
    z-index: 2; }
  label {
    display: none; }
  .hs-error-msgs,
  .hs-error-msgs label {
    color: #f15450;
    font-size: 12px; }
  input.hs-input.error, div.field.error input, div.field.error textarea, div.field.error .chzn-choices, textarea.hs-input.error {
    border-color: #f15450;
    &::placeholder {
      color: #f15450; } }
  .actions {
    &:before {
      position: absolute;
      content: "";
      top: 19px;
      right: 15px;
      @include arr(5,10,$white,r); }
    &:after {
      position: absolute;
      content: "";
      top: 22px;
      right: 20px;
      height: 4px;
      width: 17px;
      background-color: $white; } } }
.g-recaptcha {
  display: table;
  margin: 0 auto; }

.lazyload,
.lazyloading {
  opacity: 0; }

.lazyloaded {
  opacity: 1;
  transition: opacity 300ms; }

.article {
  .email-analytics {
    @include clr;
    &__text {
      float: left;
      width: 49%;
      @include r($md) {
        float: none;
        width: 100%; } }
    &__img {
      float: right;
      width: 51%;
      @include r($md) {
        float: none;
        width: 100%; }
      picture, img {
        margin: -3rem -1rem 0 -2rem;
        max-width: calc(100% + 3rem);
        display: block;
        @include r($md) {
          margin: 3rem 0 0;
          max-width: 100%; } } } } }







.out {
  height: auto;
  min-height: 100%; }

.section {
  padding: 11rem 0 10rem;
  &_blue {
    color: $white;
    background-color: $ocean-blue; }
  &_talk {
    position: relative;
    text-align: center; }
  &__info {
    position: absolute;
    left: 50%;
    top: -3rem;
    transform: translateX(-50%);
    min-width: 390px;
    padding: 0.6rem 2.9rem;
    height: 6rem;
    line-height: 6rem;
    color: $white;
    @include roboto;
    font-size: 3.6rem;
    line-height: 1.33;
    text-align: center;
    border-radius: 3rem;
    background-color: $ocean-blue;
    @include r($mob) {
      padding: 1.3rem 1.9rem;
      min-width: 280px;
      font-size: 2.1rem;
      font-weight: 700; } }
  @include r($xlg) {
    padding: 5rem 0 10rem; }
  @include r($sm) {
    padding: 2.5rem 0 5rem; } }

