$colorName: "yellow", "green", "red", "blue", "blue3", "black", "black2";
$color: $yellow, $dark-mint, $coral, $ocean-blue, $sky-blue, $dark, $cool-grey;
$colorHover: $light-gold, $light-teal, $pale-pink, $light-navy, $sky-blue, $cool-grey, $cool-grey;

@for $i from 1 through length($colorName) {
  .btn_full-#{nth($colorName, $i)} {
    border: 0;
    color: #fff;
    background: nth($color, $i);
    &:hover {
      background: nth($colorHover, $i); }
    &:active {
      background: darken(nth($colorHover, $i), 10%); } } }

@for $i from 1 through length($colorName) {
  .btn_stroke-#{nth($colorName, $i)} {
    border-color: nth($color, $i);
    color: nth($color, $i);
    background: transparent;
    &:hover {
      background: nth($colorHover, $i); }
    &:active {
      background: darken(nth($colorHover, $i), 10%); } } }

.btn-plain {
  display: inline-block;
  border: 1px solid;
  padding: 0 .5rem;
  height: 4.4rem;
  line-height: 4.4rem;
  min-width: 26rem;
  border-radius: 4.4rem;
  letter-spacing: 2px;
  font-weight: 900;
  font-size: 2.4rem;
  color: #fff;
  @include roboto;
  text-align: center;
  background: none;
  text-transform: uppercase;
  &:hover {
    background: #fff;
    color: $dark; } }

.btn-action {
  display: inline-block;
  padding: 0 1rem;
  min-width: 16.3rem;
  height: 4.4rem;
  line-height: 4.4rem;
  border-radius: 10rem;
  background-color: $yellow;
  transition: color .2s ease-in-out, background-color .2s ease-in-out, box-shadow .2s ease-in-out, border-color .2s ease-in-out;
  box-shadow: 0 1px 4px #8E6E09;
  color: #f4f4f4;
  font-size: 2.4rem;
  font-weight: 900;
  @include roboto;
  text-align: center;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  &:hover {
    background-color: $light-gold; }
  &:active {
    box-shadow: none; }
  &_white {
    color: $ocean-blue;
    box-shadow: 0 10px 30px 0 #08427c;
    background-color: $white;
    &:hover {
      color: $white;
      box-shadow: 0 5px 15px 0 #08427c;
      background-color: lighten($ocean-blue, 30); } }
  &_sm {
    font-size: 1.7rem; }
  .icon {
    margin-left: 0.3rem;
    font-size: 1.8rem;
    fill: currentColor; }
  .icon-send {
    margin-top: 1.3rem;
    vertical-align: top; } }

.field {
  @include r($lg) {
    margin-bottom: 2rem; }
  &__input {
    @include r($md) {
      padding: .8rem 1.5rem; }
    @include r($sm) {
      font-size: 1.6rem; }
    &:focus,
    &:valid,
    &.is-filled {
      ~ .field__name {
        @include r($lg) {
          top: -2.2rem;
          font-size: 1.5rem; }
        @include r($sm) {
          top: -1.2rem; } } } }
  &__name {
    @include r($lg) {
      font-size: 1.8rem; }
    @include r($sm) {
      font-size: 1.6rem; } }
  &_big {
    margin-bottom: 3rem;
    .field__input {
      border-bottom-width: 1px;
      padding: 1.2rem 1.5rem;
      @include r($md) {
        padding: .7rem 1.5rem;
        font-size: 2.1rem; }
      @include r($sm) {
        padding: .8rem 1.5rem; } } } }
.section_talk {
  .field__input,
  .hs-input {
    &:-webkit-autofill {
      -webkit-box-shadow: inset 0 0 0 1000px #fff !important;
      -webkit-text-fill-color: #000 !important;
      color: #000 !important; } } }
.section_blue {
  .field__input,
  .hs-input {
    &:-webkit-autofill {
      -webkit-box-shadow: inset 0 0 0 1000px #0855a2 !important;
      -webkit-text-fill-color: #fff !important;
      color: #fff !important; } } }
.field-info {
  margin-top: 5rem;
  font-size: 2.1rem;
  a {
    font-weight: 700;
    border-bottom: 1px solid transparent;
    transition: border-color .3s ease-in-out;
    color: inherit;
    &:hover {
      border-color: currentColor; } } }
.field-action {
  margin: 3rem 0 0;
  text-align: center; }

.field-file {
  position: relative;
  .file {
    position: absolute;
    right: 0;
    bottom: 0; }
  .field__input {
    padding-right: 4rem; } }

.file {
  display: block;
  cursor: pointer;
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3rem;
    height: 4.2rem;
    background: none;
    &:hover {
      .icon {
        fill: $sky-blue; } } }
  .icon {
    font-size: 2.2rem;
